//box
.box{border:1px solid #ddd;float:left;width:100%;
	&.boxpd{padding:30px;
		.badge{display: inline-block; line-height: 30px;width: 30px;text-align: center; background-color:#4d6f9b; padding: 0;
			&.bg_gray{background-color: #7f7f7f;}
		}
	}
}

.box1 {
    border: 1px solid #BFBFBF;
    padding: 20px;
}
.boxTop{
	// border: 7px solid #3B63C4;
	// padding:0 12px;
	// position:relative;
	// margin-bottom:20px; 
	// clear:both; 
	text-align: center;
    background-color: #F2F6FA;
    border-radius: 16px;
    padding: 40px 24px;
    margin-bottom: 30px;
}
.boxTopArea{
	p{margin:0}
	ol{padding:0;;margin:0;}
}
.boxTopArea, 
.boxTop>p{
	line-height:1.6;
	color:#005596;
	font-size: rem(20) !important;
	text-align:center;
	// background:#fff;
	// margin:-7px 0 !important;
	font-weight: 500;
	//padding:10px;
	& + p {
		margin: -16px 0 -7px !important;
	}
	@include breakpoint($sm) {
		padding: 6px;
		margin: -8px 0 -7px !important;
	}
	.point{
		text-decoration:underline;color:#1f497d !important;
	}
	.type{
		font-size:1.35em;display:block;
	}
	em {
		color: #1a1a1a;
	}
	strong {
		font-weight: 600;
	}
	.linkType {
		a {
			position: relative;
			font-weight: 500;
			color: #2F4F9D;
			background-color: #F2F6FA;
			border: 1px solid $point;
			padding: 0 35px 0 5px;
			&::after {				
				@include font-ico-outline('\e315');
				color: #fff;
				font-size: 20px;
				background-color: rgba(19, 21, 71, 1);
				position: absolute;
				top: 0;
				right: 0;
				width: 28px;
				height: 28px;
				line-height: 28px;
			}
		}
	}
}
.boxbbsWrap{
	h2{float:left;}
}

.boxType2 {
	background:#3c4559 url(/static/portal/img/cts/sub8/200200_1.jpg) no-repeat right top;
	padding:2% 4%; 
	color:#fff;
	margin-bottom:20px;
	&.bg {background:#3c4559 url(/static/portal/img/cts/sub8/200201_1.jpg) no-repeat right top;}
	.tit{
		color:#fff;
		font-size: rem(22);
		letter-spacing:-1px;
		font-weight:500;
		margin-bottom: 10px;
	}
	li{font-size:13px;margin-bottom:5px;}
	.schType{overflow:hidden;clear:both;margin-top:25px;color:#000;
		p{margin-bottom:3px !important;}
		label{color:#fff;font-size:13px;width:60px;display:inline-block;}
		.inpStyle{
			border:4px solid #5b79b9;
			height:45px;
			padding: 0 10px; 
		}
		.pa1{overflow:hidden;
			p{float:left;width:50%;
				&:first-child{width:48%;margin-right:2%;
					@include breakpoint($sm){width:100%;margin-right:0;}
				}
				@include breakpoint($sm){width:100%;}
			}
		}
		.pa2{position:relative;
			.inpStyle{background:#fff;vertical-align:middle;line-height:35px;
				>span{margin-left:0;padding-right:40px;display:block;}
				.inpStyle_t{width:100%;line-height:35px;height:35px;border:0;}
			}
			.schStyle{position:absolute;top:5px;right:0;margin-left:0;
				a{display:inline-block;width:35px;height:35px;
					&:before {
						//content: '\f002';
						display:inline-block;
						//@include fa;
						margin-right: 10px;font-size:20px;
					}
				}
			}
		}
		.pa1, 
		.pa2{clear:both;
			label{float:left;display:block;padding-top:10px;}
			>p>span{margin-left:60px;display:block;
				.inpStyle{width:100%}
			}
		}
	}
}


.numListStyle {
	&.type{overflow:hidden;
		li{float:left;margin-right:10px;}
	}
	li{text-indent:-24px;margin-left:24px;
		>span{text-indent:0;border-radius: 20px;background:#5b79b9;color:#fff;padding:0 5px;min-width:19px;line-height:1.4;display:inline-block;text-align:center;font-size:14px;}
	}
}


//box
.box2{
	background: #FAFAFA; 
	padding:20px;margin-bottom:20px;clear:both;
	&.type1{position:relative;padding:10px 20px;
		.topCenter{position:absolute; top:50%;}
		.rightBtn{text-align:right; right:20px;margin-top:-25px;}
		.badge{padding-left:0; padding-right:0;margin-top:-40px;
			>span{font-size:40px;display:inline-block; width:80px; height:80px;line-height:80px; text-align:center;}
		}
		.badge_txt{display:inline-block;text-align:left;vertical-align:top;
			p{margin-bottom:5px !important;}
		}
		.txtType{font-size:18px;font-weight:bold;color:#1f497d;
			.mDib{margin-left:15px;
				&:first-child{margin-left:0;}
			}
		}
	}
	.resultTxt {
		font-size: rem(24);
		.fc_red,
		strong {
			font-weight: 600;
			color: #3B63C4 !important;
		}
	}
}
.box3 {
	background: #FAFAFA;
    padding: 15px 20px;
    margin-bottom: 20px;
    border: 1px solid #BFBFBF;
	h4 {
		margin-top: 0 !important;
		&::before {
			content: "";
			display: inline-block;
			vertical-align: middle;
			width: 16px;
			height: 16px;
			background-image: url(/static/portal/img/ico/ico-caption.png);
			background-color: transparent;
			margin-right: 5px;
			margin-bottom: 3px;
			border-radius: 0;
		}
	}
}
.box4 {
	background-color: gainsboro;
	border: 6px solid #e5e5e5;
}

.boxInfoRel{margin-bottom: 0; height: 100px; overflow-y: auto; 
	&.box3 {padding:5px 10px;}
	h3{margin: 0 !important}
}


.box2.type2 {
	display: flex;
    align-items: center;
    justify-content: center;
    gap: 0 8%;
	padding: 20px;
    margin-bottom: 20px;
	border: 2px solid #dee0e6;
    background: none;
	@include breakpoint($sm) {
		flex-direction: column;
		align-items: flex-start;
	}
	dl {
		// width: 70%;
		// margin: 0 auto;
	}
	.bullet2{
		margin: 15px 0 4px 0;
		font-size: rem(16);
		&::before {
			content: "◆";
			display: inline-block;
			color: #3656A3;
			font-size: 10px;
			vertical-align: top;
			padding: 5px 7px 0 0;
		}
	}
	li {
		font-size: rem(15);
	}
}

.box2.bg-lightBlue {
	background: #F2F6FA;
	padding: 30px 30px;
	h2 {
		span {
			display: block;
			font-size: rem(18);
			font-weight: 400;
			color: #1a1a1a;
		}
	}
}